
import back from '@/components/back'
import noData from '@/components/noData'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
components:{
back,
noData
},
data(){
return{
title: '选择转诊医生',
user: this.$store.state.userInfo.Data,
haveMore: true,
page: 1,
list: [],
reason: [],
pageShow: false,
showConfirm: false, //是否显示弹窗
showPopup:false,//是否显示时间选择器
dateVal:"",
referralRemark:"",
defaultImg: 'this.src="' + require('../../assets/patient/pat_1_2.png') + '"',
dataApp:{},
ToDrId:"",
ToDrName:"",
PatientName:this.$route.query.PatientName,

}
},
directives: {
TransferDom
},
created(){
this.pull(false)
this.deterDate()
},
mounted(){
this.getMore()
},
beforeDestroy(){
window.onscroll = null
},
methods: {
deterDate(){
this.showPopup=false

let date = new Date();
let seperator1 = "-";
let year = date.getFullYear();
let month = date.getMonth() + 1;
let strDate = date.getDate();
if(month >= 1 && month <= 9) {
month = "0" + month;
}
if(strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
};

if(!this.dateVal){
this.dateVal = year + seperator1 + month + seperator1 + strDate;
}
},
goReferral(i){
this.showConfirm = true
this.ToDrId = i.DrId
this.ToDrName = i.DrName
},
onConfirm(){
this.$post('PlatFormAPI/SpecialistHosGroup/SaveSpecialistReferral',{
SpecialistHosGroupId:this.$route.query.SpecialistHosGroupId,
HosGroupName:this.$route.query.HosGroupName,
FromDrId: this.user.User.DoctorId,//来源医生Id(当前登录医生，必填)
FromDrName:this.user.User.DoctorName,
ToDrId: this.ToDrId,
ToDrName: this.ToDrName,
ReferralRemark:this.referralRemark,	//转诊说明
PatientId:this.$route.query.PatientId, //会员Id(必填)
PatientName: this.$route.query.PatientName,	//会员姓名(必填)

}).then(o => {
this.$toast("转诊成功")
setTimeout(() => {
this.$router.push('/teamList')
}, 2000)
this.$bridge.callhandler('ZhuanZhenSuccess', true)
})
},
pull(h){
if(this.haveMore){
this.$post('PlatFormAPI/SpecialistHosGroup/QuerySpecialistHosGroupMemberPage',{
SpecialistHosGroupId: this.$route.query.SpecialistHosGroupId,
KeyName: '',
IsHead: -1,
IsSpecialReferral: 1,
PageIndex: this.page,
PageSize: 15,
}).then(o => {
this.pageShow = true
if (!o.ReturnData || o.ReturnData.length < 15){
this.haveMore = false
}
else {
this.haveMore = true
}
if (h){
o.ReturnData.forEach(v => {
this.list.push(v)
})
}
else {
this.list = o.ReturnData
}
})
}
},

getMore(){
const el = this.$refs.el
let timer = null
window.onscroll = () => {
clearTimeout(timer)
timer = setTimeout(() => {
const top = document.documentElement.scrollTop
|| window.pageYOffset
|| document.body.scrollTop,
h = el.scrollHeight,
wh = window.innerHeight

if (h - top - wh < 100 && this.haveMore){
this.page += 1
this.pull(true)
}
}, 100)
}
}
}
}



